.footer {
  width: 100%;
  margin-top: auto;
  text-align: center;
  background-color: #f7f7f7;
}

.footer_link {
  color: #545454;
  text-decoration: underline;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer_section {
  padding-top: 25px;
  padding-bottom: 23px;
}
@media screen and (min-width: 768px) {
  .footer_section {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.footer_text {
  line-height: 1.17;
  color: #545454;
  margin: 0;
}

.footer_margin {
  margin: 0;
  position: relative;
}

/* .footer_name {
} */

.footer_icon {
  animation: beat 0.7s ease infinite alternate;
  fill: #ff6b08;
  margin-left: 3px;
  margin-right: 4px;
}

@keyframes beat {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.8);
  }
}

@media screen and (max-width: 767px) {
  .footer_text {
    font-size: 14px;
    line-height: 1.14;
    margin: 0;
    text-align: center;
  }

  .footer_section {
    padding-top: 25px;
    padding-bottom: 25px;
    text-align: center;
  }

  .footer_margin {
    margin-top: 5px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 1.14;
    text-align: center;
  }
}
